<template>
    <section>
        <div class="row">
            <div class="col-md-12 padding-t_8">
                <div class="theme-primary partition-full">
                    <span class="p-name text-white">{{ $t('Add') }}</span>
                </div>
            </div>
        </div>

        <div class="border p-3">
            <Form ref="AddGroup">
                <div class="row">
                    <div class="col-lg-12">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                    <div class="col-lg-6 mb-3 mb-lg-0" id="divtickettupelist">
                        <div class="listing">
                            <div class="table-responsive table-fix-header" id="divUserList">
                                <table id="example" class="table table-bordered dt-responsive nowrap mt-0 w-100">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th scope="col">{{ $t('CAusername') }}</th>
                                            <th scope="col">{{ $t('Department') }}</th>
                                            <th scope="col" class="text-center">{{ $t('Action') }}</th>
                                        </tr>
                                    </thead>
                                    <tbody id="UserListDiv">
                                        <tr v-for="itemData in UserList" :key="itemData">
                                            <td>{{ itemData.userName }}</td>
                                            <td>{{ itemData.departmentName }}</td>
                                            <td class='text-center single-action'><a
                                                    class='ancAddUsers round-icon-small btn-success-light d-block float-none m-auto'
                                                    href='javascript:;'
                                                    @click="AssignedUser(itemData.userId, itemData.userName, itemData.departmentName)"
                                                    :data-id="itemData.userId" :data-name="itemData.UserName"
                                                    :data-department="itemData.departmentName"><i class='fa fa-plus'
                                                        alt='Click to Add' title='Add'></i></a></td>
                                        </tr>
                                        <tr class='norecords' id='trNoRecordFond' v-if="UserList.length == 0">
                                            <td colspan='5' class='text-danger text-center'>{{ $t('NoRecordfound') }}</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                        <span id="error_User" v-if="UserList.length == 0" class="text-danger">{{ $t('CheckAtleastOneUser')
                        }}</span>
                    </div>
                    <div class="col-lg-6">
                        <div class="listing">
                            <div class="table-responsive table-fix-header">
                                <table class="table table-bordered" id="DefaultUserTable">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th scope="col">{{ $t('CAusername') }}</th>
                                            <th scope="col">{{ $t('Department') }}</th>
                                            <th scope="col" width="200">{{ $t('MaxTicketAssign') }}</th>
                                            <th scope="col" class="text-center">{{ $t('MarkAsDefault') }}</th>
                                            <th scope="col" class="text-center">{{ $t('Action') }}</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(item, i) in AssignUserList" :key="item">
                                            <td>{{ item.name }}</td>
                                            <td>{{ item.departmentName }}</td>
                                            <td class="text-center">
                                                <!-- <Field :name="'maxticketassign'" v-slot="{ errors }"
                                                    :rules="{ 'required': true }">
                                                    <input type="text" @keydown="SetMaxValue($event)"
                                                        :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                        :value="item.UserId == item.DefaultUser ? MaxTiacketAssign : ''"
                                                        name="maxticketassign" :disabled="item.Id != defaultUser"
                                                        v-model="MaxTiacketAssign" maxlength="4" tabindex="6"/>
                                                    <span v-if="errors[0]" :name="'maxticketassign' + index"
                                                        class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                </Field> -->
                                                <Field :name="'maxticketassign'" v-slot="{ errors }" :rules="{ 'required': true }">
                                                  <input type="text" @keydown="SetMaxValue($event)"
                                                    :value="item.UserId == item.DefaultUser ? MaxTiacketAssign : ''"
                                                    name="maxticketassign" :disabled="item.Id != defaultUser"
                                                    :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                    maxlength="4" tabindex="6"/> 
                                                 </Field>
                                            </td>
                                            <td class="text-center">
                                                <div class="d-inline-block"><input type="radio" v-model="defaultUser"
                                                        @click="SetValue(item.Id)" :value="item.Id" :id="item.Id"
                                                        :checked="item.UserId != item.DefaultUser" name="default"
                                                        tabindex="7"></div>
                                            </td>
                                            <td class="text-center">
                                                <a class="aRemoveUser round-icon-small btn-danger-light d-block float-none m-auto"
                                                    @click="DeleteUser(item.Id)" href="javascript:;"><i class=" fa fa-trash"
                                                        title='Add'></i></a>
                                            </td>
                                        </tr>
                                        <tr class='norecords' v-if="AssignUserList.length == 0">
                                            <td colspan='5' class='text-danger text-center'>{{ $t('NoRecordfound') }}</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                        <span id="error_DefaultUser"
                            v-if="(CheckAssignedUser && AssignUserList.length == 0) || checkOneUserSelected"
                            class="text-danger">Check Atleast One Defualt User</span>
                    </div>
                    <div class="col-lg-12 mt-3">
                        <Field name="Description" v-slot="{ errors }" :rules="{ 'required': true }">
                            <label>Description :<span class="text-danger">*</span></label>
                            <div class="form-group">
                                <textarea v-model="Description" tabindex="8" maxlength="500"
                                    :class="{ 'form-control': true, 'is-invalid': errors[0] }"></textarea>
                                <span v-if="errors[0]" name="Description" class="invalid-feedback d-block">{{ errors[0]
                                }}</span>
                            </div>
                        </Field>
                    </div>
                    <div class="col-12">
                        <div class="row flex-row-reverse">
                            <div class="col-lg-6 text-right">
                                <a @click="SubmitData(values)" href="javascript:;" class="btn btn-success"
                                    id="aGroupTicketSave" title="" data-toggle-tooltip="tooltip"
                                    data-original-title="Save"><i class="fa fa-save pr-2"></i>{{ $t('Save') }}</a>&nbsp;
                                <RouterLink to="/Ticketing/Group" class="btn btn-danger">
                                    <em class="fa fa-close"></em>
                                    {{ $t('Cancel') }}
                                </RouterLink>
                            </div>
                            <div class="col-lg-6">
                                <div class="py-2"><span class="text-danger">{{ $t('MandatoryString') }}</span></div>
                            </div>
                        </div>
                    </div>
                </div>
            </Form>
        </div>
    </section>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    created: function () {
        var vm = this;
        vm.GetSkill();
        vm.GetDepartment();
        vm.GetResolveTicketUserList();
        if (!vm.isBlank(vm.$route.params.id) && vm.$route.params.id != "0") {
            vm.GetGroupTicketData(vm.$route.params.id);
        }
        else {
            if (vm.$route.params.id == "0") {
                vm.GetGroupTicketData(0);

            }
            else {
                vm.$router.push({ name: "AddGroup", params: { id: 0 } });
                vm.GetGroupTicketData(0);
            }
        }

        // if (!vm.isBlank(vm.$route.params.id) && vm.$route.params.id != 0) {
        //     vm.GetGroupTicketData(vm.$route.params.id);
        // }
        // else if (vm.$route.params.id == "0") {
        //     vm.GetGroupTicketData(0);
        // }
        // else if (!vm.isBlank(vm.$route.params.id)) {
        //     vm.$router.push({ name: "AddGroup", params: { id: 0 } });
        //     // vm.$router.push({ path: "/Ticketing/AddGroup/0" });
        //     vm.GetGroupTicketData(0);
        // }
    },
    data() {
        return {
            UserList: [],
            AssignUserList: [],
            Description: '',
            defaultUser: '',
            GroupType: '',
            CheckAssignedUser: false,
            FormSchema: [
                {
                    layoutType: "four",
                    Data: [
                        {
                            astype: "TextField",
                            label: 'Ticket Group Name',
                            name: "TicketGroupName",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 250 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Skill'),
                            name: "SkillId",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.GetResolveTicketUserList
                            },
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Department'),
                            name: "DepartmentId",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],
                                showAddIcon: false,
                                onChange: this.GetResolveTicketUserListIs
                            },
                            validationRules: "",
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('GroupType'),
                            name: "GroupType",
                            value: "Call",
                            placeholder: "",
                            config: {
                                options: [{
                                    name: 'Call', value: 'Call'
                                }, {
                                    name: 'Routing', value: 'Escalate'
                                }],
                                showAddIcon: false,
                                onChange: this.onChangeRadio
                            },
                            validationRules: { "required": false },
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-warning",
                    text: "<i class='fa fa-save pr-2'></i>Save Of Draft",
                    isVisible: false
                }
            ],
        }
    },
    methods: {
        GetSkill: function () {
            DataServices.GetSkill().then(response => {
                response.data.forEach((item, index) => {
                    this.FormSchema[0].Data[1].config.options.push({
                        value: item.skill_id,
                        name: item.title
                    })
                })
            })
        },
        GetDepartment: function () {
            DataServices.GetDepartment().then(response => {
                response.data.forEach((item, index) => {
                    this.FormSchema[0].Data[2].config.options.push({
                        value: item.department_id,
                        name: item.department_name
                    })
                })
            })
        },
        GetResolveTicketUserList: function (e, field) {
            var vm = this;
            if (field != undefined) {
                var params = `departmentId=&skillId=${field.value}`
            }
            else {
                var params = `departmentId=&skillId=`
            }

            DataServices.GetResolveTicketUserListbydept(params).then(response => {
                vm.UserList = response.data;
            })
        },
        GetResolveTicketUserListIs: function (e, field) {
            var vm = this;
            var params = `departmentId=${field.value}&skillId=`
            DataServices.GetResolveTicketUserListbydept(params).then(response => {
                vm.UserList = response.data;
            })
        },
        AssignedUser: function (id, name, departmentname) {
            var vm = this;
            if (vm.AssignUserList.some(item => item.Id === id)) {
                vm.ShowAlert(name + ' ' + vm.$t('AlreadyExist'), "warning");
            }
            else {
                vm.AssignUserList.push({
                    Id: id,
                    name: name,
                    departmentName: departmentname,
                    UserId: id
                })
            }
        },
        DeleteUser: function (id) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Group")), function () {
                vm.AssignUserList = jQuery.grep(vm.AssignUserList, function (value) {
                    return value.Id != id;
                });
            });
        },
        GetGroupTicketData: function (id) {
            var vm = this;
            var params = `ticketGroupId=${id}`
            DataServices.GetGroupTicketData(params).then(response => {
                vm.FormSchema[0].Data[0].value = response.data.Data[0].ticket_group_name;
                vm.Description = response.data.Data[0].description;
                vm.FormSchema[0].Data[2].value = response.data.Data[0].department_id;
                vm.FormSchema[0].Data[3].value = response.data.Data[0].Group_type;
                vm.MaxTiacketAssign = response.data.Data[0].max_ticket_assign;
                vm.defaultUser = response.data.Data[0].default_user_id;
                response.data.Data.forEach((item, index) => {
                    vm.AssignUserList.push({
                        Id: item.user_id,
                        name: item.username,
                        departmentName: item.department_name,
                        UserId: item.user_id,
                        DefaultUser: item.default_user_id
                    })
                })
            })
        },
        SubmitData: function () {
            debugger;
            var vm = this;
            var userIds = ''
            vm.AssignUserList.forEach((item, index) => {
                if (userIds == '') {
                    userIds += item.Id
                }
                else {
                    userIds += ',' + item.Id
                }

            })

            vm.$refs.AddGroup.validate().then(result => {
                if ((vm.AssignUserList.length == 0 || vm.isBlank(vm.defaultUser))) {
                    vm.CheckAssignedUser = true;
                    vm.ShowAlert(vm.$t('CorrectHighlightedErrors'), "warning");
                    vm.checkOneUserSelected = true;
                    return false;
                }
                if (result) {
                    var params = `groupTicketId=${vm.$route.params.id}&groupName=${vm.FormSchema[0].Data[0].value}&description=${vm.Description}&departmentId=${vm.FormSchema[0].Data[2].value == '' ? 0 : vm.FormSchema[0].Data[2].value}&groupType=${vm.FormSchema[0].Data[3].value}&maxTiacketAssign=${vm.MaxTiacketAssign}&userIds=${userIds}&defaultUser=${vm.defaultUser}`
                    DataServices.AddTicketGroup(params).then(response => {
                        if (response.data == true) {
                            vm.ShowAlert(vm.$t('GroupHasAdded'), "success");
                            vm.$router.push({ name: 'GroupListing' });
                        }
                        else {
                            vm.ShowAlert(vm.$t('GroupHasAdded'), "success");
                        }
                    })
                }
            })
        },
        SetMaxValue: function (value) {
            var vm = this;
            if ([46, 8, 9, 27, 13, 110, 190].includes(value.keyCode) || (value.keyCode == 65 && (value.ctrlKey || value.metaKey)) || (value.keyCode == 67 && (value.ctrlKey || value.metaKey)) || (value.keyCode == 86 && (value.ctrlKey || value.metaKey)) || (value.keyCode == 88 && (value.ctrlKey || value.metaKey)) || (value.keyCode >= 35 && value.keyCode <= 39)) {
                vm.MaxTiacketAssign = '';
                vm.checkOneUserSelected = false;
                return;
            }

            if ((value.shiftKey || (value.keyCode < 48 || value.keyCode > 57)) && (value.keyCode < 96 || value.keyCode > 105)) {
                value.preventDefault();
            }

            vm.MaxTiacketAssign = value.key;
            vm.checkOneUserSelected = false;
        }
        ,
        isNumber: function (evt) {
            evt = (evt) ? evt : window.event;
            var charCode = (evt.which) ? evt.which : evt.keyCode;
            if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                evt.preventDefault();;
            } else {
                return true;
            }
        },
        SetValue: function (id, e) {
            var vm = this;
            vm.MaxTiacketAssign = '';
            vm.defaultUser = id;
        },
        onChangeRadio: function (e, field) {
            var vm = this;
            vm.FormSchema[0].Data[3].value = e.target.dataset.value;
        }
    }
}

</script>